.cvContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    gap: 30px;
    padding: 100px 50px 0;
    background-image: url('../../assets/backgrounds/cvBackground.svg');
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

.cvWrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 1000px;
    padding: 30px 20px;
    border-radius: 50px;
    background-color: rgba(235, 235, 235, .9);
    backdrop-filter: blur(10px);
}

.keyInfoSection {
    display: flex;
    align-items: center;
    gap: 50px;
    width: 100%;
}

.imageContainer {
    position: relative;
    display: flex;
    width: 315px;
    height: 315px;
    border-radius: 30px;
    object-fit: fill;
    overflow: hidden;

    & > img {
        transform: translateX(-80px);
    }
}

.personalInfoContainer {
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding-top: 15px;

    & > p {
        font-family: 'Raleway', sans-serif;
        margin: 0;
    }
}

.fullName {
    font-family: 'Raleway', sans-serif;
    font-style: italic;
    font-size: 36px;
    margin: 0;
}

.contactInfo {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 15px;
    
    & > h3 {
        margin: 0;
        font-family: 'Raleway', sans-serif;
    }

    & > div {
        display: flex;
        align-items: center;
        gap: 10px;
    }
    & > div > img {
        width: 27px;
        height: 27px;
    }
    & > div > a {
        font-family: 'Raleway', sans-serif;
        text-decoration: none;
        color: #000
    }
}

.infoSection {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.subtitle {
    font-size: 22px;
    font-family: 'Raleway', sans-serif;
    color: #292929;
    border-bottom: 2px solid #cecece;
}

.positionSection {
    display: flex;
    flex-direction: column;

    & > p:first-of-type {
        margin-top: 15px;
    }

    & > p {
        font-family: 'Nunito Sans', sans-serif;
        font-size: 18px;
    }

    & > ul {
        padding-left: 20px;
        list-style: none;

        & > li {
            display: flex;
            align-items: center;
            font-family: 'Nunito Sans', sans-serif;
            font-size: 17px;
            font-style: italic;
        }

        & > li:before {
            content: '>';
            margin-right: 5px;
            font-family: 'Nunito Sans', sans-serif;
            font-size: 18px;
        }
    }
}

.projectsWrapper {
    display: flex;
    width: 100%;
    padding: 10px 0;
    overflow: hidden;

    &:hover > .projectsContainer {
        animation-play-state: paused;
    }
}

.experienceSection  {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.projectsContainer {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px 30px;
    width: 100%;
    padding: 0 30px;
}

.educationPlacesTable {
    border-spacing: 20px 10px;
}

.organizationName
{
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
}

.date,
.skillName {
    text-align: start;
    font-family: 'Raleway', sans-serif;
}

.skillsContainer {
    display: flex;
    flex-wrap: wrap;
    gap: 5px 20px;

    & > p {
        padding: 5px 15px;
        font-family: 'Nunito Sans', sans-serif;
        border-radius: 15px;
        background-color: #cecece;
    }
}

.languageContainer {
    display: flex;
    gap: 5px;

    & > p:last-of-type {
        color: #595959;
    }
}